<template>
  <div v-click-outside>
    <input type="text" :value="formatDate" />
    <div class="pannel" v-if="isVisible">
      <div class="pannel-nav">
        <span @click="prevYear"> &lt;</span>
        <span @click="prevMonth"> &lt; &lt;</span>
        <span> {{ time.year }}å¹´</span>
        <span> {{ time.month + 1 }}æ</span>
        <span @click="nextMonth"> &gt; &gt;</span>
        <span @click="nextYear"> &gt;</span>
      </div>
      <div class="pannel-content">
        <div class="days">
          <!-- æ¸²æåºå¨å æ é¢ -->
          <span v-for="i in 7" :key="i" class="cell cell-day">
            {{ weekDays[i - 1] }}
          </span>
          <!-- å­è¡ä¸åçä¸ä¸ªè¡¨æ ¼ -->
          <div v-for="i in 6" :key="'_' + i">
            <!-- ä¸æ¯å½æå°±åç° -->
            <span
              :class="[
                {
                  notCurrentMonth: !isCurrentMonth(
                    visibeDays[(i - 1) * 7 + (j - 1)]
                  ),
                },
                {
                  today: isToday(visibeDays[(i - 1) * 7 + (j - 1)]),
                },
                {
                  select: isSelect(visibeDays[(i - 1) * 7 + (j - 1)]),
                },
              ]"
              class="cell"
              v-for="j in 7"
              :key="j"
              @click="chooseDate(visibeDays[(i - 1) * 7 + (j - 1)])"
            >
              {{ visibeDays[(i - 1) * 7 + (j - 1)].getDate() }}
            </span>
          </div>
        </div>
      </div>
      <div class="pannel-footer">ä»å¤©</div>
    </div>
  </div>
</template>

<script>
import * as utils from '../utils/index'
export default {
  name: 'vat-date-picker',
  props: {
    value: {
      type: Date,
      default: () => {
        return new Date()
      }
    }
  },
  data () {
    let { year, month } = utils.getYearMonthDay(this.value)
    return {
      time: { year, month },
      weekDays: ['æ¥', 'ä¸', 'äº', 'ä¸', 'å', 'äº', 'å­'],
      isVisible: false
    }
  },
  methods: {
    // éæ©ä»å¤©æ¥æ
    chooseDate (date) {
      // åéå®ä¹å è¿è¦è·æ°timeæ é¢éé¢çå¹´æ
      this.time.year = utils.getYearMonthDay(date).year
      this.time.month = utils.getYearMonthDay(date).month
      this.$emit('input', date)
    },
    // æ£æµæ¯ä¸æ¯å½å¤©
    isToday (date) {
      let { year, month, day } = utils.getYearMonthDay(new Date())
      let { year: y, month: m, day: d } = utils.getYearMonthDay(date)
      return year === y && month === m && day === d
    },
    // æ£æµæ¯ä¸æ¯éä¸­çæ¥æ
    isSelect (date) {
      // ä¼ å¥çå¹´ææ¥åç¨æ·çå¹´ææ¥ä¸æ ·ä¸ä¸æ ·
      let { year, month, day } = utils.getYearMonthDay(this.value)
      let { year: y, month: m, day: d } = utils.getYearMonthDay(date)
      return year === y && month === m && day === d
    },
    // æ£æµæ¯ä¸æ¯å½æ
    isCurrentMonth (date) {
      //éè¿å¯¹æ¯this.valueçå¹´åæå¯¹æ¯æ¯ä¸æ¯å½æ
      let { year, month } = utils.getYearMonthDay(utils.getDate(this.time.year, this.time.month, 1))
      let { year: y, month: m } = utils.getYearMonthDay(date)
      return year === y && month === m
    },
    prevMonth () {
      let d = utils.getDate(this.time.year, this.time.month, 1)
      d.setMonth(d.getMonth() - 1)
      this.time = utils.getYearMonthDay(d)
    },
    nextMonth () {
      let d = utils.getDate(this.time.year, this.time.month, 1)
      d.setMonth(d.getMonth() + 1)
      this.time = utils.getYearMonthDay(d)
    },
    prevYear () {
      let d = utils.getDate(this.time.year, this.time.month, 1)
      d.setFullYear(d.getFullYear() - 1)
      this.time = utils.getYearMonthDay(d)
    },
    nextYear () {
      let d = utils.getDate(this.time.year, this.time.month, 1)
      d.setFullYear(d.getFullYear() + 1)
      this.time = utils.getYearMonthDay(d)
    },
    blur () {
      this.isVisible = false
    },
    focus () {
      this.isVisible = true
    }
  },
  computed: {
    visibeDays () {
      // è·åå½åæ¯å¨å 
      const { year, month } = utils.getYearMonthDay(utils.getDate(this.time.year, this.time.month, 1))
      // è·åå½åæä»½ç¬¬ä¸å¤© new
      let currentFirstDay = utils.getDate(year, month, 1)
      // åçæä¸ä¸ªå½åæ¥æ
      // è·åå½åå¨å æå¤©æ°è¿ç§»å å¤©
      let week = currentFirstDay.getDay()
      // å½åå¼å§å¤©æ°
      let startDay = currentFirstDay - week * 60 * 60 * 24 * 1000
      // å¾ªç¯42å¤©
      let arr = []
      for (let i = 0; i < 42; i++) {
        arr.push(new Date(startDay + i * 60 * 60 * 24 * 1000))
      }
      return arr
    },
    formatDate () {
      const { year, month, day } = utils.getYearMonthDay(this.value)
      return `${year}-${month + 1}-${day}`
    }
  },
  mounted () {
  }
}
</script>

<style lang="stylus" scoped>
.pannel {
  width: 32 * 7px;
  position: absolute;
  background: white;
  clear: both;
  box-shadow: 2px 2px 2px pink, -2px -2px 2px pink;

  .pannel-nav {
    display: flex;
    justify-content: space-around;
    height: 30px;

    span {
      cursor: pointer;
      user-select: none;
    }
  }

  .pannel-content {
    .cell {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      width: 32px;
      height: 32px;
      font-weight: bold;
    }

    .cell:hover, .select {
      border: 1px solid blue;
      box-sizing: border-box;
    }

    .cell-day:hover {
      border: 0px solid blue;
    }
  }

  .pannel-footer {
    height: 30px;
  }
}

.notCurrentMonth {
  color: gray;
}

.today {
  background: hotpink;
  border-radius: 4px;
  color: #fff;
}
</style>